@use 'base/variables' as v;
@use 'base/mixins' as m;
// vista movil
.elecciones {
    display: grid;
    grid-template-rows: repeat(2, 30rem);
}

// .cat1__enlace {
//     @include m.tablet {
//         @include m.grid(2, 3rem);
//     }
// }
.eleccion {
    display: grid;
    place-items: center;
    background-color: v.$secundario;
    border-radius: 1rem;
    margin: 1rem;
    &__contenido {
        padding: 2rem;
    }
    &:hover {
        cursor: pointer;
    }
}

.MenuCategoriaMovil {
    margin: 2rem auto;
    background-color: v.$primario;
    text-align: center;
    color: v.$blanco;
    width: 100%;
    padding: 1rem;
    border: none;
    outline: none;
}

// vista PC
.eleccionPC {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    background-color: v.$blanco;
    &__contenido {
        padding: 2rem;
        display: grid;
        place-items: center;
        text-align: center;
        cursor: pointer;
        background-color: v.$recuadros;
    }
    &__titulo {
        text-transform: uppercase;
        color: v.$titulo;
        font-size: 3.4rem;
        margin: 2rem 0;
    }
}

//  Propiedades
.tutores,
.infantes {
    display: none;
}

.cat1__enlace,
.cat2__video,
.cat3__documento {
    display: none;
}

.visible {
    display: block;
}

.activo {
    background-color: darken($color: v.$recuadros, $amount: 30);
}

// Categorias de materiales
.material__clasificacion {
    margin: 0 0 2rem 0;
    @include m.grid(3, 2rem);
    &__boton {
        @include m.boton(v.$terciario, v.$negro);
        text-align: center;
        border-radius: 3rem;
        color: v.$blanco;
        margin: 2rem 0 0 0;
        padding: 1rem;
        cursor: pointer;
        &-activo {
            background-color: darken($color: v.$recuadros, $amount: 30);
        }
    }
}